.header{
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  height: auto;
  .header-top{
    display: flex;
    
    padding: 0 15px;
    align-items: center;
    img{
      max-width: 100%;
      height: auto;

    }
    .action{
      display: inline-flex;
      .b-icon button{
        margin: 15px;
        padding: 15px;
      }
    }
  }
//search

    .search, .search-md{
      display: flex;
      justify-content: space-between;
      border: 1px solid grey;
      border-radius: 3px;
      margin: 10px 15px;
      .s-input{
        width: 100%;
        height: 47px;
        border:none;
        outline: none;
        background-color: transparent;
        .s-input:focus, .s-input-md:focus{
          outline: none !important;
          border-color: #719ECE;
          box-shadow: 0 0 10px #719ECE;
        }
      }
      .s-button{
        border: none;
        background-color: transparent;
        cursor: pointer;
      }
      .s-button-md{
        background-color: #999aa2;
        border: none;
        padding: 20px;
        color: #fff;
        cursor: pointer;
      }

    }
}

//main menu navigation

span, small{
  display: block;
  text-align: center;
}
//set visibility submenu start
.nav-btn .sub-menu-banner{
  display: none;
}
.nav-btn:hover .sub-menu-banner{
  display: flex;
  flex-wrap: wrap;
  position: absolute;
  list-style: none;

  background-color: inherit;

  padding: 20px 0;
  width: 100%;
  left: 0;
  z-index: 1;

  h1{
    color: $w-color;
    width: 100%;
    text-align: left;
    margin-left: 30px;

  }
  li{
    padding: 0 10px;
    margin-top: 15px;
    margin-left: 30px;
    width: 30%;
    height: auto;
    text-align: left;

  }
}
//set visibility submenu end
